import React from 'react'
import { Helmet } from 'react-helmet'
import StripeCheckout from '../components/StripeCheckout'

/**
 * Checkout Page Template
 * 
 * Reusable template for checkout pages that handles all boilerplate:
 * - Helmet/SEO configuration
 * - Test mode detection and banner
 * - StripeCheckout component integration
 * - Responsive layout
 * 
 * @param {Object} offerData - Offer configuration object (required)
 * @param {string} successUrl - URL to redirect after successful payment (optional)
 * @param {string} cancelUrl - URL to redirect on cancel (optional)
 */
const CheckoutPageTemplate = ({ 
  offerData, 
  successUrl, 
  cancelUrl 
}) => {
  // Validate required props
  if (!offerData) {
    throw new Error('CheckoutPageTemplate requires offerData prop')
  }

  // Determine if we're in test mode
  const isTest = process.env.GATSBY_ISTEST === 'true'
  
  // Select the correct price ID based on test mode
  const priceId = isTest ? offerData.stripe.test_priceId : offerData.stripe.price_id

  return (
    <div className="min-h-screen bg-white">
      <Helmet>
        <title>Checkout - {offerData.meta.title}</title>
        <meta name="description" content={`Complete your purchase of ${offerData.meta.title}`} />
        <meta name="robots" content="noindex, nofollow" />
      </Helmet>

      {/* Test Mode Banner */}
      {isTest && (
        <div className="bg-yellow-400 text-black text-center py-2 px-4 font-bold">
          ‚ö†Ô∏è TEST MODE - Using test Stripe keys and test price IDs
        </div>
      )}

      {/* Embedded Checkout */}
      <div className="w-full min-h-screen flex items-start justify-center p-4 pt-12">
        <div className="w-full max-w-2xl shadow-lg rounded-lg p-6 bg-white">
          <StripeCheckout
            priceId={priceId}
            successUrl={successUrl}
            cancelUrl={cancelUrl}
            isTest={isTest}
          />
        </div>
      </div>
    </div>
  )
}

export default CheckoutPageTemplate
